import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Card, Stack, Avatar, Divider, Badge, Typography } from '@mui/material';
import { TimeLine } from './';
// ----------------------------------------------------------------------
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor: '#44b700',
    color: '#44b700'
  }
}));

export default function BookingDetails() {
  const theme = useTheme();

  return (
    <>
      <Card sx={{ px: 3, py: 1 }}>
        <Stack spacing={1.5}>
          <Stack direction="row" alignItems="center" spacing={3} justifyContent="space-between">
            <Stack direction="row" spacing={3}>
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_14.jpg"
                sx={{ width: '32px', height: '32px' }}
              />
              <Stack direction="column" spacing={0.5}>
                <Typography variant="subtitle1" sx={{ fontSize: '14px', fontWeight: '500', lineHeight: '17px' }}>
                  Mrs.Meena's order
                </Typography>
                <Typography
                  variant="subtitle2"
                  sx={{ color: '#637381', fontSize: '10px', fontWeight: '500', lineHeight: '10px' }}
                >
                  id # 0011223344
                </Typography>
              </Stack>
            </Stack>

            <Stack>
              <Icon
                icon="mdi:dots-vertical"
                style={{
                  paddingTop: '2px',
                  paddingBottom: '2px',
                  paddingLeft: '5px',
                  paddingRight: '5px',
                  fontSize: '30px',
                  color: '#212B36',
                  border: '1px solid #919EAB24'
                }}
              />
            </Stack>
          </Stack>

          <Stack direction="row" justifyContent="center" spacing={2.5}>
            <Icon
              icon="mdi:calendar-check"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFE7D9',
                fontSize: '20px',
                color: '#B72136'
              }}
            />
            <Icon
              icon="mdi:phone"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFF7CD',
                fontSize: '20px',
                color: '#FFC107'
              }}
            />
            <Icon
              icon="mdi:email"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#C8FACD',
                fontSize: '20px',
                color: '#00AB55'
              }}
            />
          </Stack>

          <Divider />

          <Stack spacing={1}>
            <Stack direction="row">
              <Typography
                variant="subtitle2"
                sx={{
                  color: '#637381',
                  fontSize: '10px',
                  display: 'flex',
                  alignItems: 'center',
                  ml: 2,
                  width: '80px'
                }}
              >
                Phone
              </Typography>
              <Typography variant="subtitle2" sx={{ fontSize: '14px' }}>
                +91 9966603052
              </Typography>
            </Stack>

            <Stack direction="row">
              <Typography
                variant="subtitle2"
                sx={{
                  color: '#637381',
                  fontSize: '10px',
                  display: 'flex',
                  alignItems: 'center',
                  ml: 2,
                  width: '80px'
                }}
              >
                Email
              </Typography>
              <Typography variant="subtitle2" sx={{ fontSize: '14px' }}>
                vjasti99@gmail.com
              </Typography>
            </Stack>

            <Stack direction="row">
              <Typography
                variant="subtitle2"
                sx={{
                  color: '#637381',
                  fontSize: '10px',
                  display: 'flex',
                  alignItems: 'center',
                  ml: 2,
                  width: '80px'
                }}
              >
                Address
              </Typography>
              <Typography variant="subtitle2" sx={{ fontSize: '14px', width: '120px' }}>
                46 Main Street Trash Colony Vi
              </Typography>
            </Stack>

            <Stack direction="row">
              <Typography
                variant="subtitle2"
                sx={{
                  color: '#637381',
                  fontSize: '10px',
                  display: 'flex',
                  alignItems: 'center',
                  ml: 2,
                  width: '80px'
                }}
              >
                Refferal
              </Typography>
              <Stack
                direction="row"
                spacing={1}
                sx={{ border: '2px solid #919EAB24', borderRadius: '40px', padding: '3px' }}
              >
                <Avatar
                  alt="My Avatar"
                  src="/static/mock-images/avatars/avatar_12.jpg"
                  sx={{ width: '24px', height: '24px' }}
                />
                <Typography variant="subtitle2" sx={{ fontSize: '14px', color: '#979797' }}>
                  Ramesh Kodali
                </Typography>
              </Stack>
            </Stack>

            <Stack direction="row">
              <Typography
                variant="subtitle2"
                sx={{
                  color: '#637381',
                  fontSize: '10px',
                  display: 'flex',
                  alignItems: 'center',
                  ml: 2,
                  width: '80px'
                }}
              >
                Last visit
              </Typography>
              <Typography variant="subtitle2" sx={{ fontSize: '14px' }}>
                12th Aug, 2021
              </Typography>
            </Stack>
          </Stack>

          <Stack>
            <TimeLine />
          </Stack>
        </Stack>
      </Card>
    </>
  );
}
